import React from 'react';

import {Spinner, Combobox} from '@salesforce/design-system-react';

import {$, baseUrl, GetParams} from '../lib/grax-data-lake';

import _ from 'lodash';

import {comboboxFilterAndLimit} from '../lib/utils';


export default class GRAXObjectCombobox extends React.Component {
    constructor(props) {
		super(props);

		this.state = {
            options: [],
			inputValue: '',
            selection: [],
            isLoaded: false,
            error: null
		};
    }
    
    componentDidMount() {
        $.ajax({
            type: 'GET',
            //dataType: 'jsonp',
            url: baseUrl + '/grax-app/grax-data-lake/getDataTypes' + GetParams({action:'load'}),
            data: {},
            success: function(data) {
                if(this.props.excludeIds) {
                    data = _.filter(data, (item) => {
                        return this.props.excludeIds.indexOf(item.id) === -1;
                    });
                }

                this.setState({
                    options: data,
                    isLoaded: true
                });
            }.bind(this),
            error: function(xhr, status, err) {
                console.error(xhr, status, err);

                this.setState({
                    isLoaded: true,
                    error: err.toString()
                });
            }.bind(this)
        });
    }

    render() {
        if (this.state.error) {
            return <div>Error: {this.state.error}</div>;
        } else if (!this.state.isLoaded) {
            return (
                <div style={{ position: 'relative', height: '2rem' }}>
                    <Spinner size="small" variant="base" />
                </div>
            );
        } else {
            return (
                <React.Fragment>                    
                    <Combobox
                        classNameMenu={'scrollable-GRAXObjectCombobox'}
                        events={{
                            onChange: (event, { value }) => {
                                this.setState({
                                    inputValue: value
                                });

                                if (this.props.onChange) {
                                    this.props.onChange(event, value);
                                }
                            },
                            onRequestRemoveSelectedOption: (event, data) => {
                                this.setState({
                                    inputValue: '',
                                    selection: data.selection,
                                });

                                if (this.props.onRequestRemoveSelectedOption) {
                                    this.props.onRequestRemoveSelectedOption(event, data.selection);
                                }
                            },
                            onSelect: (event, data) => {
                                this.setState({
                                    inputValue: '',
                                    selection: data.selection,
                                });

                                if (this.props.onSelect) {
                                    this.props.onSelect(event, data.selection);
                                }
                            },
                        }}
                        labels={{
                            placeholder: 'Search GRAX Table',
                        }}
                        options={comboboxFilterAndLimit({
                            inputValue: this.state.inputValue,
                            limit: 1000,
                            options: this.state.options,
                            selection: this.state.selection
                        })}
                        selection={this.state.selection}
                        value={this.state.inputValue}
                        variant="inline-listbox"
                        required={true}
                    />
                </React.Fragment>
            );
        }
    }
}