///* global $ */
import React from 'react';
import _ from 'lodash';

import SLDSSection from './SLDSSection';
import GRAXObjectCombobox from './GRAXObjectCombobox';
import GRAXFieldsCombobox from './GRAXFieldsCombobox';
import GRAXDeleteDataTable from './GRAXDeleteDataTable';
import GRAXFilterGeneration from './GRAXFilterGeneration';

import {Button} from '@salesforce/design-system-react';


const ELASTIC_SORTABLE_TYPES = ['text', 'date', 'number'];


export default class GRAXDelete extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            selectedDataIndex: null,
            retrieved: false,
            retrievedCounter: 0,
            _graxFields: null,
            _graxFieldsSelection: null,
            _graxFieldsSelectionList: null,
            _filterFunction: ((dataList) => dataList),
            _getFilterQuery: (() => null),
            _filterValuesFields: [],
            _graxSortableFields: null
        }
    }

    hasFieldSelected() {
        return this.state._graxFields != null && this.state._graxFieldsSelectionList != null && this.state._graxFieldsSelectionList.length > 0;
    }
    
    render() {
        return (
            <div>
                <SLDSSection isCollapsible={true} isOpen={true} title="Select the GRAX Table">
                    <GRAXObjectCombobox
                        onSelect={(event, data) => {
                            this.setState({
                                selectedDataIndex: data[0]
                            });
                        }}
                        excludeIds={['graxgrax_loggingindex']}
                        onRequestRemoveSelectedOption={(event, data) => {
                            this.setState({
                                retrieved: false,
                                selectedDataIndex: null,
                                _graxFieldsSelection: null,
                                _graxFieldsSelectionList: null
                            });
                        }}
                    />

                    <div className="slds-m-bottom_large"></div>
                </SLDSSection>
                
                <SLDSSection isCollapsible={true} isOpen={true} title="Search GRAX Table">
                    {
                        this.state.selectedDataIndex != null
                        ? (
                            <GRAXFieldsCombobox
                                dataIndex={this.state.selectedDataIndex.id}
                                onLoad={(e, _graxFieldsSelection, _graxFields, _graxFieldsSelectionList) => {
                                    console.log('onLoad', _graxFieldsSelection, _graxFieldsSelectionList, _graxFields);

                                    this.setState({
                                        _graxFields: _graxFields,
                                        _graxFieldsSelection: _graxFieldsSelection,
                                        _graxFieldsSelectionList: _graxFieldsSelectionList,
                                        _graxSortableFields: _.reduce(_graxFields, (sortableFields, elasticField, i) => {
                                            if(ELASTIC_SORTABLE_TYPES.indexOf(elasticField.type) !== -1) {
                                                sortableFields.push(elasticField.id);
                                            }
                            
                                            return sortableFields;
                                        }, [])
                                    });
                                }}
                                onChange={(e, _graxFieldsSelection, _graxFields, _graxFieldsSelectionList) => {
                                    console.log('onChange', _graxFieldsSelection, _graxFieldsSelectionList);

                                    var retrievedCounter = this.state.retrievedCounter;

                                    if(_graxFieldsSelectionList.length > this.state._graxFieldsSelectionList.length) {
                                        retrievedCounter++;
                                    }

                                    this.setState({
                                        retrievedCounter: retrievedCounter,
                                        _graxFieldsSelection: _graxFieldsSelection,
                                        _graxFieldsSelectionList: _graxFieldsSelectionList
                                    });
                                }}
                            />
                        )
                        : 'No GRAX Table selected...'
                    }

                    <div className="slds-m-bottom_large"></div>
                </SLDSSection>

                <SLDSSection isCollapsible={true} isOpen={true} title="Filter Records">
                    {/*<Input
                        iconLeft={
                            <InputIcon
                                assistiveText="Search"
                                name="search"
                                category="utility"
                            />
                        }
                        assistiveText={{ label: 'Search Term' }}
                        id="record-query"
                        placeholder="Search"
                    />*/}
                    {this.state._graxFieldsSelectionList != null && (
                        <GRAXFilterGeneration
                            SObjectType={this.state.selectedDataIndex.label}
                            filterableFieldOptions={this.state._graxFields}
                            onFilterValueChangeHandler={(_filterFunction, _filterValuesFields, _getFilterQuery) => {
                                this.setState({
                                    retrievedCounter: this.state.retrievedCounter,
                                    _filterFunction: _filterFunction,
                                    _getFilterQuery: _getFilterQuery,
                                    _graxFieldsSelectionList: this.state._graxFieldsSelectionList,
                                    _filterValuesFields: _filterValuesFields
                                });
                            }}
                        />
                    )}

                    <div className="slds-m-bottom_large"></div>
                </SLDSSection>

                <SLDSSection isCollapsible={true} isOpen={true} title="GRAX Stored Records">
                    <div className="-x-small-buttons--horizontal slds-p-bottom_large">
					    <Button label="Retrieve" variant="brand" onClick={() => {
                            this.setState({
                                retrieved: this.state.selectedDataIndex != null,
                                retrievedCounter: this.state.retrievedCounter + 1
                            })
                        }} disabled={!this.hasFieldSelected()} />
                    </div>

                    {this.state.retrieved && this.state._graxFieldsSelectionList && (
                        <GRAXDeleteDataTable
                            key={'GRAXDeleteDataTable_' + this.state.retrievedCounter}
                            renderKey={'GRAXDeleteDataTable_' + this.state.retrievedCounter}
                            columns={this.state._graxFieldsSelectionList}
                            hiddenColumns={this.state._filterValuesFields}
                            dataIndex={this.state.selectedDataIndex.id}
                            filterFunction={this.state._filterFunction}
                            filterQuery={this.state._getFilterQuery()}
                            sortableColumns={this.state._graxSortableFields}
                        />
                    )}
                </SLDSSection>
            </div>
        );
    }
}