/* eslint-disable no-restricted-globals */
import React from "react";

import {
  Spinner,
  Button,
  DataTable,
  DataTableColumn,
  Modal,
  Checkbox
} from "@salesforce/design-system-react";
import SLDSPaginator from "./SLDSPaginator";

import { $, baseUrl, GetParams } from "../lib/grax-data-lake";
import { moment, SF_DATETIME_REGEXP, DATE_FORMAT, DATETIME_FORMAT, TZ } from "../lib/utils";

import _ from "lodash";

const ROWSXPAGE = 100;

export default class GRAXDeleteDataTable extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      sortColumn: "Id",
      sortDirection: "asc",
      chunks: [],
      items: [],
      extraInfo: {},
      page: 0,
      selection: [],
      isLoaded: false,
      isLoading: false,
      deleteConfirmIsOpen: false,
      deleteConfirmIsChecked: false,
      deleteConfirmCallback: () => {},
      deleteConfirmTotal: 0
    };
  }

  shouldComponentUpdate(nextProps, nextState) {
    return (
      this.props.renderKey !== nextProps.renderKey ||
      this.state.items !== nextState.items ||
      this.state.selection !== nextState.selection ||
      this.state.isLoaded !== nextState.isLoaded ||
      this.state.isLoading !== nextState.isLoading ||
      this.state.sortColumn !== nextState.sortColumn ||
      this.state.sortDirection !== nextState.sortDirection ||
      this.state.deleteConfirmIsOpen !== nextState.deleteConfirmIsOpen ||
      this.state.deleteConfirmIsChecked !== nextState.deleteConfirmIsChecked
    );
  }

  getDataHandler(page, sortColumn, sortDirection) {
    const _sort = sortColumn + " " + sortDirection;

    this.setState({
      isLoading: true,
      page: page,
      sortColumn: sortColumn,
      sortDirection: sortDirection
    });

    $.ajax({
      type: "GET",
      //dataType: 'jsonp',
      url:
        baseUrl +
        "/grax-app/grax-data-lake" +
        GetParams({
          action: "load",
          dataIndex: this.props.dataIndex,
          _select:
            _.union(this.props.columns, this.props.hiddenColumns, [
              "Id",
              "graxorgid",
              "attributes.type"
            ]).join(",") || "Id",
          _q: this.props.filterQuery,
          _limit: ROWSXPAGE,
          _from: ROWSXPAGE * page,
          _sort: _sort,
          _includeMetadata: true
        }),
      /*data: {
                _select: this.props.columns.join(',')
            },*/
      success: (data) => {
        let results = data.results;

        if (this.props.filterFunction) {
          results = this.props.filterFunction(results);
        }

        results = _.map(results, (item, i) => {
          _.forEach(item, (val, key) => {
            if (typeof val == "string") {
              if (SF_DATETIME_REGEXP.test(val)) {
                const m = moment(val);

                if (m.isValid()) {
                  item[key] = m
                    .tz(TZ)
                    .format(m._i.indexOf(":") !== -1 ? DATETIME_FORMAT : DATE_FORMAT);
                }
              }
            }

            if (typeof val == "boolean") {
              item[key] = val ? "true" : "false";
            }
          });

          return {
            ...item,
            ...{ id: i.toString() }
          };
        });

        this.setState({
          page: 0,
          data: results,
          extraInfo: data.extraInfo || { total: 0, size: ROWSXPAGE, totalRetrieved: 0 },
          items: results,
          isLoaded: true,
          isLoading: false
        });
      },
      error: (xhr, status, err) => {
        console.error(xhr, status, err);

        this.setState({
          isLoaded: true,
          isLoading: false,
          items: [],
          error: err.toString()
        });
      }
    });
  }

  componentDidMount() {
    this.getDataHandler(0, this.state.sortColumn, this.state.sortDirection);
  }

  handleChanged(event, data) {
    this.setState({ selection: data.selection.slice(0, 10) });
    console.log(event, data);
  }

  handleSort(sortColumn, ...rest) {
    if (sortColumn.property) {
      this.getDataHandler(this.state.page, sortColumn.property, sortColumn.sortDirection);
    }
  }

  render() {
    return this.state.isLoaded ? (
      <div>
        <div style={{ position: "relative" }}>
          {this.state.isLoading && <Spinner size="large" variant="brand" />}
          <DataTable
            assistiveText={{
              actionsHeader: "actions",
              columnSort: "sort this column",
              columnSortedAscending: "asc",
              columnSortedDescending: "desc",
              selectAllRows: "all rows",
              selectRow: "select this row"
            }}
            fixedLayout
            items={this.state.items}
            id="GRAXDeleteDataTable"
            onRowChange={this.handleChanged.bind(this)}
            onSort={this.handleSort.bind(this)}
            selection={this.state.selection}
            selectRows={true}
          >
            {this.props.columns.map(columnName => (
              <DataTableColumn
                key={"col" + columnName}
                id={columnName}
                isSorted={this.state.sortColumn === columnName}
                label={columnName} // TODO
                property={columnName}
                sortable={
                  this.props.sortableColumns == null ||
                  this.props.sortableColumns.indexOf(columnName) !== -1
                }
                sortDirection={this.state.sortDirection}
              />
            ))}
          </DataTable>
        </div>

        <div className="-x-small-buttons--horizontal slds-p-top_small">
          <SLDSPaginator
            totalItems={this.state.items.length > 0 ? this.state.extraInfo.total : 0}
            pageSize={ROWSXPAGE}
            onChangePage={(page, isInitialLoad) => {
              if (!isInitialLoad) {
                this.getDataHandler(page - 1, this.state.sortColumn, this.state.sortDirection);
              }
            }}
          />
        </div>

        <div className="-x-small-buttons--horizontal slds-p-top_medium">
          <Button
            label="Delete Selection"
            disabled={this.state.selection.length < 1 || this.state.isLoading}
            onClick={() => {
              this.setState({
                deleteConfirmIsOpen: true,
                deleteConfirmIsChecked: false,
                deleteConfirmTotal: this.state.selection.length,
                deleteConfirmCallback: () => {
                  if (
                    this.state.deleteConfirmIsChecked &&
                    confirm("Are you sure that you want to delete the selected records?")
                  ) {
                    $.ajax({
                      type: "POST",
                      url:
                        baseUrl +
                        "/grax-app/grax-data-lake/deleteAPI" +
                        GetParams({
                          action: "delete"
                        }),
                      contentType: "application/json; charset=utf-8",
                      data: JSON.stringify(
                        _.map(this.state.selection, item => {
                          return _.pick(item, [
                            "Id",
                            "graxorgid",
                            "_id",
                            "_type",
                            "_index",
                            "attributes.type"
                          ]);
                        })
                      ),
                      success: data => {
                        alert(data.state);
                      }
                    });

                    this.setState({
                      selection: [],
                      deleteConfirmIsOpen: false,
                      deleteConfirmCallback: () => {},
                      deleteConfirmTotal: 0
                    });
                  }
                }
              });
            }}
          />

          {/*<Button label="Delete All" disabled={this.state.isLoading} onClick={() => {
                        this.setState({
                            deleteConfirmIsOpen: true,
                            deleteConfirmIsChecked: false,
                            deleteConfirmTotal: this.state.extraInfo.total,
                            deleteConfirmCallback: (() => {
                                if(this.state.deleteConfirmIsChecked && confirm('Are you sure that you want to delete the filtered records?')) {
                                    $.ajax({
                                        type: 'POST',
                                        url: baseUrl + '/grax-app/grax-data-lake/deleteAll' + GetParams({
                                            action: 'delete',
                                        }),
                                        contentType: "application/json; charset=utf-8",
                                        data: JSON.stringify({
                                            dataIndex: this.props.dataIndex,
                                            _q: this.props.filterQuery,
                                            _sort: this.state.sortColumn + ' ' + this.state.sortDirection
                                        }),
                                        success: function(data) {
                                            alert(data.state);
                                        }.bind(this)
                                    });

                                    this.setState({
                                        selection: [],
                                        deleteConfirmIsOpen: false,
                                        deleteConfirmCallback: (() => {}),
                                        deleteConfirmTotal: 0
                                    });
                                }
                            })
                        });
                    }} />*/}
        </div>

        <Modal
          dismissible={true}
          footer={[
            <div key="agree" className="slds-p-bottom_small">
              <Checkbox
                assistiveText={{
                  label: "I understand the risks"
                }}
                id="delete-confirm"
                labels={{
                  label: "I understand the risks"
                }}
                onChange={e => {
                  this.setState({
                    deleteConfirmIsChecked: e.target.checked
                  });
                }}
              />
            </div>,
            <Button
              key="cancelBtn"
              label="Cancel"
              onClick={() => {
                this.setState({
                  deleteConfirmIsOpen: false,
                  deleteConfirmIsChecked: false
                });
              }}
            />,
            <Button
              key="deleteBtn"
              label="Delete"
              onClick={this.state.deleteConfirmCallback}
              variant="destructive"
              disabled={!this.state.deleteConfirmIsChecked}
            />
          ]}
          isOpen={this.state.deleteConfirmIsOpen}
          onRequestClose={() => {
            this.setState({
              deleteConfirmIsOpen: false,
              deleteConfirmIsChecked: false
            });
          }}
          prompt="error"
          size="medium"
          align="top"
          dismissOnClickOutside={false}
          title={
            <span>
              Delete <b>{this.state.deleteConfirmTotal}</b> record(s)
            </span>
          }
        >
          <div className="slds-m-around_medium">
            Please confirm that you want to delete these record(s) from your GRAX Datalake.
            <br />
            After you confirm deletion, these records are NOT recoverable.
          </div>
        </Modal>
      </div>
    ) : (
      <div style={{ position: "relative", height: "2rem" }}>
        <Spinner size="medium" variant="brand" />
      </div>
    );
  }
}
