import React from 'react';
import _ from 'lodash';

import {Spinner, Combobox} from '@salesforce/design-system-react';

import {$, baseUrl, GetParams} from '../lib/grax-data-lake';

import {comboboxFilterAndLimit} from '../lib/utils';

import {getCookie, setCookie} from '../lib/utils';

var selectedDataIndex;
var cookieSubfix = 'Fields';


export default class GRAXFieldsCombobox extends React.Component {
    constructor(props) {
		super(props);

		this.state = {
            options: [],
			inputValue: '',
            selection: [{id: 'Id', label: 'Id', type: 'text'}],
            isLoaded: false,
            error: null
        };
        
        this.getMappedFieldList = this.getMappedFieldList.bind(this);
    }


    getMappedFieldList(selection) {
        return _.map(selection || this.state.selection, (item) => {
            return item.id;
        });
    }

    onLoadHandler(event, selection) {
        var selectedFieldList = this.getMappedFieldList(selection);

        if(this.props.onLoad) {
            this.props.onLoad(event, selection, this.state.options, selectedFieldList);
        }
    }

    onChangeHandler(event, selection) {
        var selectedFieldList = this.getMappedFieldList(selection);

        if (this.props.onChange) {
            this.props.onChange(event, selection, this.state.options, selectedFieldList);
        }

        setCookie(selectedDataIndex + cookieSubfix, JSON.stringify(selectedFieldList));
    }

    componentDidMount() {
        this._isMounted = true;

        selectedDataIndex = this.props.dataIndex;

        if(this.props.cookieSubfix) {
            cookieSubfix += this.props.cookieSubfix;
        }

        $.ajax({
            type: 'GET',
            // dataType: 'jsonp',
            url: baseUrl + '/grax-app/grax-data-lake/getDataTypeFields/' + selectedDataIndex + GetParams({action:'load'}),
            data: {},
            success: (data) => {
                if(this._isMounted) {
                    var options = _.map(data, (val, key) => ({
                        id: key,
                        label: key,
                        type: val
                    }));

                    this.setState({
                        options: options,
                        isLoaded: true
                    });

                    var selectedFieldsCookie = getCookie(selectedDataIndex + cookieSubfix);
                    if(!selectedFieldsCookie.startsWith('[')) {
                        selectedFieldsCookie = '[]';
                    }

                    var selectedFields = JSON.parse(selectedFieldsCookie);

                    this.setState({
                        selection: selectedFields.length > 0 ? (
                            _.sortBy(
                                _.reduce(options, (_selection, item) => {
                                    if(selectedFields.indexOf(item.id) !== -1) {
                                        _selection.push(item);
                                    }

                                    return _selection;
                                }, [])
                            , (item) => {
                                return selectedFields.indexOf(item.id);
                            })
                        ) : this.state.selection
                    });
                }

                this.onLoadHandler(null, this.state.selection);
            },
            error: (xhr, status, err) => {
                console.error(xhr, status, err);

                if(this._isMounted) {
                    this.setState({
                        isLoaded: true,
                        error: err.toString()
                    });
                }
            }
        });
    }

    componentWillUnmount() {
        this._isMounted = false;
    }

    /*shouldComponentUpdate(nextProps, nextState) {
        return this.state.isLoaded !== nextState.isLoaded;
    }*/

    render() {
        if (this.state.error) {
            return <div>Error: {this.state.error}</div>;
        } else if (!this.state.isLoaded) {
            return (
                <div style={{ position: 'relative', height: '2rem' }}>
                    <Spinner size="small" variant="base" />
                </div>
            );
        } else {
            return (
                <React.Fragment>
                    <Combobox
                        classNameMenu={'scrollable-GRAXFieldsCombobox'}
                        events={{
                            onChange: (event, { value }) => {
                                this.setState({
                                    inputValue: value
                                });
                            },
                            onRequestRemoveSelectedOption: (event, data) => {
                                this.setState({
                                    inputValue: '',
                                    selection: data.selection,
                                });

                                this.onChangeHandler(event, data.selection);
                            },
                            onSelect: (event, data) => {
                                this.setState({
                                    inputValue: '',
                                    selection: data.selection,
                                });

                                this.onChangeHandler(event, data.selection);
                            },
                            onRequestClose: (event, data) => {
                                if (this.props.onSelect && this.state.inputValue) {
                                    var selectedFieldList = this.getMappedFieldList(data.selection);

                                    this.props.onSelect(event, [{
                                        id: this.state.inputValue,
                                        label: this.state.inputValue
                                    }], this.state.options, selectedFieldList);
                                }
                            }
                        }}
                        labels={{
                            placeholder: 'Select Option',
                        }}
                        options={comboboxFilterAndLimit({
                            inputValue: this.state.inputValue,
                            limit: 1000,
                            options: this.state.options,
                            selection: this.state.selection
                        })}
                        selection={this.state.selection}
                        value={this.state.inputValue}
                        // variant={this.props.variant || 'inline-listbox'}
                        required={true}
                        multiple={true}
                    />
                </React.Fragment>
            );
        }
    }
}